@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500&display=swap);
.Header {
  background: #100e10;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  left: 0px;
  z-index: 99 !important;
}

.HeaderContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 55px;
  padding: 0px 20px;
}

.Header a {
  text-decoration: none;
}

.Header .logo {
  color: #555;
}

.Header .options .option {
  margin: 0px 10px;
  color: #fff;
}

.added {
  position: absolute;
  margin: auto;
  right: 60px;
  top: 0px;
  color: #c8ff01;
  opacity:0;
  font-size:12px;
}

.added.ani {
  animation-name: added;
  animation-direction: alternate;
  animation-iteration-count: infinite;
  animation-duration: 1s;
}

@keyframes added {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.7;
  }
}

.CartItem{background:#222;display:flex;flex-direction:column;padding:5px;border-radius:3px;margin-bottom:10px}.CartItem .itemName{padding:5px;color:#ffee00}.CartItem .cartContainer{display:flex;justify-content:space-between}.CartItem .cartContainer .cartImage{width:95px;height:90px;object-fit:cover;object-position:left top;border-radius:3px}.CartItem .cartContainer .cartInfo{width:100%;height:90px;padding-left:8px;display:flex;flex-direction:column;align-items:flex-start;justify-content:space-between}.CartItem .cartContainer .cartInfo .cartPrice{color:#ffee00}.CartItem .cartContainer .cartInfo .cartRemove{width:75%;background:#f01734;color:#fff;padding:0px 6px;font-size:13px;padding-bottom:1px;border-radius:2px}.CartItem .cartContainer .cartInfo .cartRemove:focus{outline:none;border:none}.CartItem .cartContainer .cartInfo .cartQuantity{width:70%;display:flex;justify-content:space-around}.CartItem .cartContainer .cartInfo .cartQuantity .quantity{color:#ffee00;font-size:14px;padding:0px 5px;display:inline-block}.CartItem .cartContainer .cartInfo .cartQuantity button{background:#ff00bf;color:#fff;width:30px;height:20px;padding-bottom:3px;border-radius:3px;display:flex;justify-content:center;align-items:center}.CartItem .cartContainer .cartInfo .cartQuantity button:focus{outline:none;border:none}

.CartDropdown{width:300px;height:480px;background:#100e10;position:fixed;top:55px;right:0px;z-index:99;transition:all 0.4s;border-top:2px solid #555}.CartDropdown.hiddenCart{transform:translateX(100%)}.CartDropdown .cartDropdown-container{width:100%;height:400px;background:#100e10;position:absolute;top:0px;right:0px;overflow-y:scroll;color:#fff;padding:9px}.CartDropdown .cart-buttons{position:absolute;left:0px;right:0px;bottom:10px;margin:auto;width:100%;background:#000;display:flex;justify-content:space-evenly}.CartDropdown .cart-buttons button{background:#8e562e}.CartDropdown .cart-buttons .clearAll{background:#c91717 !important}

.CartIcon{position:relative;width:50px;height:50px;display:flex;justify-content:center;align-items:flex-start;cursor:pointer}.CartIcon .cart-bag{font-size:30px;display:flex;justify-content:center;align-items:flex-start}.CartIcon .cart-count{position:absolute;top:0px;right:0px;background:#5c520f;background:linear-gradient(to right, #11580f, #ad0079);color:#fff;width:25px;height:25px;border-radius:50%;display:flex;justify-content:center;align-items:center;font-size:12px;transition:all 0.5s}.CartIcon .cartCountAnim{animation-name:count;animation-direction:alternate;animation-iteration-count:infinite;animation-duration:1s}@keyframes count{from{transform:scale(1)}to{transform:scale(1.4)}}

.ItemCollection { 
 background:#ffffff; 
 height:350px; 
 display:flex;
 flex-direction: column;
 border-radius:3px;
 position:relative;
 color:#111;
}

.ItemCollection > img {
 width:100%;
 height:78%; 
 object-fit: cover;
 object-position:left top ;
}

.ItemCollection > div { 
 height:20%;
 display:flex;
 justify-content: space-between; 
 padding:0px 10px;
 align-items: center;
}
 
.cartBtn {
 position:absolute; 
 padding:5px 10px; 
 right:6px;
 bottom:25%; 
 cursor:pointer;
 background: linear-gradient(to right, #8e562e, rgb(6, 184, 6));
 color:#ffffff;
 border-radius:4px;
}
.shop_collection{margin:15px;display:grid;grid-template-columns:repeat(auto-fit, minmax(200px, 1fr));grid-gap:20px;color:white}

.PreviewColletion {
  margin-bottom:10px;
  z-index: -1;
  padding:10px;
} 
 
.PreviewColletionContainer {
  margin: 15px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 20px;
  color: white;
} 

