CSS rules to sp

body {
 overflow-x: hidden !important; 
}

* {
 font-family: Ubuntu, operator mono, arial,'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif !important;
}

button:focus {
 outline: none !important; 
 border: none !important;
} 

/* width */
::-webkit-scrollbar {
 width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
 background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
 background: #999;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
 background: #555;
}
